import React, { useState } from 'react'
import { Button, List, ListItem, ListItemText, Modal } from '@material-ui/core';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import { db } from './firebase';
import { makeStyles } from '@material-ui/core/styles';
import './Todo.css'


const useStyles = makeStyles((theme) => ({
    paper: {
      position: 'absolute',
      width: 400,
      backgroundColor: theme.palette.background.paper,
      border: '2px solid #000',
      boxShadow: theme.shadows[5],
      padding: theme.spacing(2, 4, 3),
    },
  }));

function Todo(props) {
    const classes = useStyles();
    const [open, setOpen] = useState(false);
    const [input, setInput] = useState('')

    const handleOpen = () => {
        setOpen(true)
    }

    const updateTodo = () => {
        db.collection('todos').doc(props.text.id).set({
            todo: input
        }, { merge: true})

        setOpen(false)
    }

    return (
        <>
        <Modal 
            open={open}
            onClose={e => setOpen(false)}
            >
                <div className={classes.paper}>
                    <h1>Edit Your Todo</h1>
                    <input placeholder={props.text.todo} value={input} onChange={ event => setInput(event.target.value)} />
                    <Button onClick={updateTodo}>Update Todo</Button>
                </div>
            </Modal>
        <List className="todo__list">
            <ListItem>
                <ListItemText primary={props.text.todo} secondary="COmplete It" />
            </ListItem>
            <Button onClick={e => setOpen(true)}>Edit</Button>
            <DeleteForeverIcon className="todo__delete" onClick={event => db.collection('todos').doc(props.text.id).delete()} ></DeleteForeverIcon>
        </List>
        </>
    )
}

export default Todo
